.faq {
    background: #fff;
  
    .container-fluid {
      width: 100%;
      padding: 0;
      margin: 0;
    }
    .container {
      max-width: 1440px;
      margin: 0 auto;
      padding: 48px 60px 70px 60px;
    }
  
    .faq {
      display: grid;
      place-items: self-start;
  
      h2 {
        margin: 0;
        font-weight: 500;
        color: #0a2244;
        padding: 0px 200px 0 0;
      }
  
      .question {
        display: grid;
        padding: 28px 0;
        gap: 28px;
        border-bottom: 1px solid #e1e4ec;
        width: 100%;
  
        h2 {
          margin: 16px 0;
          position: relative;
          cursor: pointer;
          &::after {
            position: absolute;
            right: 0;
            top: 10px;
            content: url("./icons/plus.svg");
          }
        }
  
        .answer {
          display: none;
          max-width: 840px;
          width: 100%;
          opacity: 0;
          visibility: hidden;
  
          li {
            list-style: square;
            font-family: "General Sans Variable";
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            line-height: 28px;
            color: #616d82;
          }
        }
      }
  
      .active {
        h2 {
          &::after {
            content: url("./icons/minus.svg");
          }
        }
      }
    }
  }
  